import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit'
import { postsAPI, APIPostsResponseType, APICommentsResponseType } from '../../API'

export const fetchPostsByDate = createAsyncThunk<void, void>('/posts/fetchPostsByDate', async (_, { dispatch }) => {
  const response = await postsAPI.getPostsByDate()
  dispatch(postsSlice.actions.getPosts(response))
})

export const fetchPostsByViews = createAsyncThunk<void, void>('/posts/fetchPostsByViews', async (_, { dispatch }) => {
  const response = await postsAPI.getPostsByViews()
  dispatch(postsSlice.actions.getPosts(response))
})

export const fetchRemovePost = createAsyncThunk<void, string>('/posts/fetchRemovePost', async (_id) => {
  await postsAPI.removePost(_id)
})

export const fetchTags = createAsyncThunk<string[], void>('/posts/fetchTags', async () => {
  return await postsAPI.getTags()
})

export const fetchComments = createAsyncThunk<APICommentsResponseType[], void>('/posts/fetchComments', async () => {
  return await postsAPI.getComments()
})

export const initValues: APIPostsResponseType = {
  _id: '',
  title: '',
  user: {
    avatarUrl: '',
    fullName: '',
    email: '',
    _id: '',
  },
  createdAt: '',
  viewsCount: 0,
  tags: [],
  comments: [],
  text: '',
  updatedAt: '',
  imageUrl: '',
}

type initialPostsStateType = {
  posts: {
    items: APIPostsResponseType[],
    status: string,
  },
  tags: {
    items: string[],
    status: string,
  },
  comments: {
    items: APICommentsResponseType[],
    status: string,
  }
  findByTag: string
  postsByTag: APIPostsResponseType[]
}

const initialState: initialPostsStateType = {
  posts: {
    items: [],
    status: 'loading',
  },
  tags: {
    items: [],
    status: 'loading',
  },
  comments: {
    items: [],
    status: 'loading',
  },
  findByTag: '',
  postsByTag: [],
}

const postsSlice = createSlice({
  name: 'posts',
  initialState,
  reducers: {
    getPosts(state, actions: PayloadAction<APIPostsResponseType[]>) {
      state.posts.status = 'panding'
      try {
        state.posts.items = actions.payload
        state.posts.status = 'loaded'
      }
      catch (error) {
        state.posts.items = []
        state.posts.status = 'error'
      }
    },
    addFindByTag(state, action: PayloadAction<string>) {
      state.findByTag = action.payload
    },
    getPostsByTag(state) {
      state.postsByTag = state.posts.items.filter((obj) => obj.tags.some((tag) => tag === state.findByTag))
    }
  },
  extraReducers: (builder) => {
    builder
      //get tegs
      .addCase(fetchTags.pending, (state) => {
        state.tags.status = 'panding'
      })
      .addCase(fetchTags.fulfilled, (state, actions: PayloadAction<string[]>) => {
        state.tags.items = actions.payload
        state.tags.status = 'loaded'
      })
      .addCase(fetchTags.rejected, (state) => {
        state.tags.items = []
        state.tags.status = 'error'
      })
      // get comments
      .addCase(fetchComments.pending, (state) => {
        state.tags.status = 'panding'
      })
      .addCase(fetchComments.fulfilled, (state, actions: PayloadAction<APICommentsResponseType[]>) => {
        state.comments.items = actions.payload
        state.tags.status = 'loaded'
      })
      .addCase(fetchComments.rejected, (state) => {
        state.tags.items = []
        state.tags.status = 'error'
      })
      // remove post
      .addCase(fetchRemovePost.pending, (state, action: PayloadAction<undefined, string, { arg: string }>) => {
        state.posts.items = state.posts.items.filter(obj => obj._id !== action.meta.arg)
      })
  },
})

export const postsReducer = postsSlice.reducer

export const { addFindByTag, getPostsByTag } = postsSlice.actions
